<template>
  <div>
    <!-- 筛选操作区 -->
    <el-row class="filter">
      <el-col :span="4" 
        >代号：<el-input v-model="userQuery.codeName"
          ><el-button
            slot="append"
            @click="codeNameFilter"
            icon="el-icon-search"
          ></el-button></el-input
      ></el-col>
      <el-col :span="4"
        >名称：<el-input v-model="userQuery.name"
          ><el-button
            slot="append"
            @click="nameFilter"
            icon="el-icon-search"
          ></el-button></el-input
      ></el-col>
      <el-col :span="4">
        类型：<el-select
          clearable
          @change="typeSelectChange"
          v-model="filterType"
        >
          <el-option
            v-for="item in clientTypeList"
            :key="item.index"
            :value="item"
          ></el-option>
        </el-select>
      </el-col>
      <el-col :span="4">
        评级：<el-select clearable v-model="filterRank">
          <el-option
            v-for="item in clientRankList"
            :key="item.index"
            :value="item"
          ></el-option>
        </el-select>
      </el-col>
      <el-col :offset="4" :span="2">
        <el-button class="btn" type="primary" @click="add">新建</el-button>
      </el-col>
      <el-col :span="2">
        <el-button class="btn" type="danger" @click="removePreview">删除</el-button>
      </el-col>
    </el-row>
    <!-- 表单区 -->
    <el-row>
      <el-table @select="rowclick" border :data="tableData">
        <el-table-column type="selection" width="50"></el-table-column>
        <el-table-column type="index" width="50" label="序号"></el-table-column>
        <el-table-column
          v-for="item in tableList"
          :prop="item.prop"
          :width="item.width"
          :label="item.label"
          :key="item.index"
        ></el-table-column>
        <el-table-column label="操作" width="80">
          <template slot-scope="scope">
            <el-button @click="edit(scope)" type="text" size="small"
              >编辑</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <!-- 新建对话框 -->
    <el-dialog title="新建" :visible.sync="isNewDialogDisplay">
      <el-form
        id="dialog-form"
        :model="userInfor"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="代号" prop="codeName">
          <el-input v-model="userInfor.codeName"></el-input>
        </el-form-item>
        <el-form-item label="名称" prop="name">
          <el-input v-model="userInfor.name"></el-input>
        </el-form-item>
        <el-form-item label="联系方式" prop="phoneNumber">
          <el-input v-model="userInfor.phoneNumber"></el-input>
        </el-form-item>
        <el-form-item label="地区" prop="zone">
          <el-select style="width: 100%" v-model="userInfor.zone">
            <el-option
              v-for="item in zoneList"
              :key="item.index"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="类型" prop="clientType">
          <el-select style="width: 100%" v-model="userInfor.clientType">
            <el-option
              v-for="item in clientTypeList"
              :key="item.index"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="评级" prop="clientRank">
          <el-select style="width: 100%" v-model="userInfor.clientRank">
            <el-option
              v-for="item in clientRankList"
              :key="item.index"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-input
            type="textarea"
            :autosize="{ minRows: 3, maxRows: 6 }"
            v-model="userInfor.remark"
          ></el-input>
        </el-form-item>
      </el-form>
      <el-row type="flex" justify="end">
        <el-col :span="3">
          <el-button
            size="medium"
            @click="isNewDialogDisplay = false"
            type="info"
            >取消</el-button
          >
        </el-col>
        <el-col :span="3">
          <el-button size="medium" @click="addUser" type="primary"
            >确定</el-button
          >
        </el-col>
      </el-row>
    </el-dialog>
    <!-- 删除对话框 -->
    <el-dialog
      id="dialog"
      title="删除用户"
      :visible.sync="isDeleteDialogDisplay"
    >
      <el-row style="font-size: 18px">即将从系统中删除以下公司:</el-row>
      <el-row>
        <el-col style="font-size: 18px" :span="6">公司：</el-col>
        <el-col :span="18">
          <div
            style="font-size: 18px"
            v-for="item in selectData"
            :key="item.index"
          >
            {{ item.name }}
          </div>
        </el-col>
      </el-row>
      <el-row type="flex" justify="end">
        <el-col :span="4">
          <el-button
            size="medium"
            @click="isDeleteDialogDisplay = false"
            type="info"
            >取消</el-button
          >
        </el-col>
        <el-col :span="4">
          <el-button size="medium" @click="remove(selectData)" type="primary"
            >确定</el-button
          >
        </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>

<script>
import {
  getSupplierAndCustomer,
  InsertSupplierAndCustomer,
  UpdateSupplierAndCustomer,
  getClientRank,
  getClientType,
  DeleteSupplierAndCustomer,
} from "network/authority.js";

import { getSupplierAndCustomerProcess } from "service/Authority/authority.js";

export default {
  data() {
    return {
      tableList: [
        { label: "代号", prop: "codeName", width: "100" },
        { label: "名称", prop: "name", width: "200" },
        { label: "联系方式", prop: "phoneNumber", width: "150", isFix: "left" },
        { label: "类型", prop: "clientType", width: "150", isFix: "left" },
        { label: "地区", prop: "zone", isFix: "left", width: "120" },
        { label: "评级", prop: "clientRank", width: "100" },
        { label: "备注", prop: "remark" },
      ],
      tableData: [],
      pageNumber: 1,
      pageSize: 10,
      isNewDialogDisplay: false,
      isDeleteDialogDisplay: false,
      userInfor: {},
      emptyInfor: {},
      selectData: null,
      zoneList: ["中国", "法国", "美国"],
      clientTypeList: [],
      clientRankList: [],
      rules: {
        codeName: [
          { required: true, message: "请输入代号", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" },
        ],
        name: [{ required: true, message: "请输入名称", trigger: "blur" }],
        phoneNumber: [
          { required: true, message: "请输入联系方式", trigger: "blur" },
        ],
        clientType: [
          { required: true, message: "请选择时间", trigger: "change" },
        ],
        zone: [
          {
            required: true,
            message: "请至少选择一个活动性质",
            trigger: "change",
          },
        ],
        clientRank: [
          { required: true, message: "请选择活动资源", trigger: "change" },
        ],
        remark: [
          { required: true, message: "请填写活动形式", trigger: "blur" },
        ],
      },
      filterRank: null,
      filterType: null,
      userQuery: null,
      selectData: [],
      isUpdate: null,
    };
  },
  created() {
    this.userInital();
  },
  methods: {
    //初始化
    userInital() {
      this.userQuery = getSupplierAndCustomerProcess();
      this.getUsers(this.userQuery);
      this.getRankList();
      this.getTypeList();
    },
    //根据条件获得用户列表
    getUsers(data) {
      getSupplierAndCustomer(data).then((res) => {
        if (res.success) {
          this.$message.success("获取客户供应商列表成功");
          this.tableData = res.result.items;
          console.log(this.tableData);
        } else {
          this.$$message.error("获取客户供应商列表失败");
        }
      });
    },
    //获得评级
    getRankList() {
      getClientRank().then((res) => {
        if (res.success) {
          this.clientRankList = res.result;
        }
      });
    },
    //获得类型
    getTypeList() {
      getClientType().then((res) => {
        if (res.success) {
          this.clientTypeList = res.result;
        }
      });
    },
    //添加用户
    add() {
      this.isUpdate = false;
      this.isNewDialogDisplay = true;
      this.userInfor = this.emptyInfor;
    },
    //添加或更新用户确认
    addUser() {
      if (this.isUpdate) {
        UpdateSupplierAndCustomer(this.userInfor).then((res) => {
          this.$message.success("更新用户成功");
        });
      } else {
        InsertSupplierAndCustomer(this.userInfor).then((res) => {
          this.$message.success("添加用户成功");
        });
      }
      this.isNewDialogDisplay = false;
      this.userInfor = this.emptyInfor;
    },
    //编辑用户
    edit(scope) {
      this.isUpdate = true;
      this.userInfor = scope.row;
      this.isNewDialogDisplay = true;
      console.log(scope.row);
    },
    //代号筛选
    codeNameFilter() {
      this.getUsers(this.userQuery);
    },
    //名称筛选
    nameFilter() {
      this.getUsers(this.userQuery);
    },
    //类型筛选
    typeSelectChange() {
      if (this.filterType == "") {
        this.userQuery.clientType = 0;
      } else {
        this.clientTypeList.forEach((item) => {});
      }
    },
    //点击选择框
    rowclick(selection, row) {
      this.selectData = selection;
    },
    //删除客户
    removePreview() {
      if (this.selectData.length == 0) {
        this.$message.error("请选择要删除的客户或供应商");
      } else {
        this.isDeleteDialogDisplay = true;
      }
    },
    //删除确认
    remove(selection) {
      var arr = new Array(selection.length);
      selection.forEach((item, index) => {
        arr[index] = item.id;
      });
      DeleteSupplierAndCustomer(arr).then((res) => {
        if (res.success) {
          this.$message.success("删除用户成功");
          this.isDeleteDialogDisplay = false;
          window.location.reload();
        }
      });
    },
  },
};
</script>

<style scoped>
.filter .el-input,
.filter .el-select {
  width: 65% !important;
}
.filter{
  text-align: left;
}
.el-row {
  margin-bottom: 10px;
}
.btn{
  width:100px;
}
/* #dialog-form .el-form-item{
    margin-bottom: 10px;
} */
</style>